import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { TrainersDataService } from '../trainer-list/trainers-data.service';
import { trainer } from '../trainer-list/trainers';

@Component({
  selector: 'app-add-trainer',
  templateUrl: './add-trainer.component.html',
  styleUrls: ['./add-trainer.component.scss']
})
export class AddTrainerComponent implements OnInit {

  tr:trainer

  id:number;
  name:string;
  course:string;
  email:string;
  constructor(private router: Router, private service: TrainersDataService) {
   
   }

  ngOnInit() {
  }

  submit(){
    this.tr.trainer_id = this.id;
    this.tr.trainer_name = this.name;
    this.tr.course_name= this.course;
    this.tr.trainer_email= this.email;
    console.log(this.tr)
    this.service.addData(this.tr)
    this.router.navigateByUrl("default/trainers");
  }

}
