import { Injectable } from '@angular/core';
import { trainer } from './trainers';

@Injectable({providedIn: 'root'})
export class TrainersDataService {
    
    trainers: trainer[]= []
    tr: trainer;
    constructor() {
        this.trainers.push(new trainer(1,"Sanghmitr", "Angular","sanghmitr@persistent.com"));
        this.trainers.push(new trainer(2,"Ishan", "HTML","Ishan@persistent.com"));
        this.trainers.push(new trainer(3,"Devraj", ".NET","Devraj@persistent.com"));

     }
     
     addData(tr:trainer){
         this.trainers.push(new trainer(this.tr.trainer_id, this.tr.trainer_name, this.tr.course_name, this.tr.trainer_email ))
     }
     getTrainerList(){
         return this.trainers;
     }
}