import { Component, Input, OnInit } from '@angular/core';
import * as Highcharts from 'highcharts';

@Component({
  selector: 'app-bar',
  templateUrl: './bar.component.html',
  styleUrls: ['./bar.component.scss']
})
export class BarComponent implements OnInit {

  Highcharts = Highcharts;
  chartOptions = {};

  @Input() data = [];
  @Input() dataLables= [];
  
  constructor() { }

  ngOnInit() {
        this.chartOptions= {
            chart: {
                type: 'column'
            },
            title: {
                text: ''
            },
            
            xAxis: {
                type: 'category',
                labels: {
                    rotation: -45,
                    style: {
                        fontSize: '13px',
                        fontFamily: 'Verdana, sans-serif'
                    }
                }
            },
            yAxis: {
                min: 0,
                title: {
                    text: 'No. of Students'
                }
            },
            legend: {
                enabled: false
            },
            tooltip: {
                pointFormat: 'No. of students <b>{point.y:.1f}</b>'
            },
            credits: {
                enabled: false
            },
            series: [{
                name: 'No. of Enrollments',
                data: [
                    ['Angular', 60],
                    ['React', 40],
                    ['Vue', 20],
                    ['Java', 30],
                    ['C++', 35],
                    ['.NET', 28],
                   
                ],
                dataLabels: {
                    enabled: true,
                    rotation: -90,
                    color: '#FFFFFF',
                    align: 'right',
                    format: '{point.y:.1f}', // one decimal
                    y: 10, // 10 pixels down from the top
                    style: { 
                        fontSize: '13px',
                        fontFamily: 'Verdana, sans-serif'
                    }
                }
            }],
            
            
        }

        setTimeout(() => {
            window.dispatchEvent(
              new Event('resize')
            );
          }, 300)
    }

    
}